html {
  scroll-behavior: smooth;
}

body {
  background-color: #1a1a1a;
}

.header {
  background-image: url("../assets/vector/vector.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
}

.about {
  background-image: url("../assets/vector/vector.svg");
  background-position: left;
  background-repeat: no-repeat;
  background-size: 100%;
}

::-webkit-scrollbar {
  width: 10px;
  background-color: black;
}

::-webkit-scrollbar-thumb {
  background-color: #FF9142;
  border-radius: 10px;
}

nav ul li {
  opacity: 0.5;
}

nav ul li:hover {
  opacity: 1 !important;
  transition: 0.5s ease;
}

.single-project-heading:hover div {
  margin-left: 10px;
  transition: 0.5s ease;
}

.cursor {
  position: fixed;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  background-image: url('../assets/vector/vector.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  pointer-events: none;
  z-index: -1;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
    .header, .about {
        background-size: 70%;
    }
}